.HomePage {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 80%;
}

.hero-text {
  color: white;
  text-align: center;
  font-size: 4rem;
  span {
    color: #037fff;
  }
}

.homePageSub {
  text-align: center;
  font-size: 2rem;
}

.icons {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 1.5rem 0;
  .iconHolder {
    margin-right: 1rem;
    .icon {
      font-size: 3rem;
      color: #a4acc4;
      transition: all 0.4s ease-in-out;
    }

    .Github:hover {
      color: #fafafa;
    }
    .Ln:hover {
      color: rgb(40, 103, 178);
    }
  }
}
